<template>
  <div class="hello">
    <h1>{{ msg }}</h1>
    <leaveingMessageComponent></leaveingMessageComponent>
  </div>
</template>

<script>
import leaveingMessageComponent from './leaveing-message/LeaveingMessage'
export default {
  name: 'HelloWorld',
  components: {leaveingMessageComponent},
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
</style>



